<template>
  <div>
    <nav class="navbar navbar-expand-sm navbar-light fixed-top itdmnav">
      <div class="container">
        <div class="navbar-header">
          <div class="d-inline-flex" @click.prevent="backtrack">
            <button class="btn  mr-1">
              <i class="fa fa-arrow-left fa-lg" aria-hidden="true"></i>
            </button>
            <div class="minilogo"></div>
          </div>
        </div>
      </div>
    </nav>
    <main>
      <div class="content_wrapper" style="align-items: start;">
        <div
          class="job_wrapper col col-sm-8 col-md-6  col-lg-3 col-xl-4 container-fluid container-sm"
        >
          <form class="job_form col">
            <p class="error">{{ error }}</p>
            <qrcode-stream @decode="onDecode" @init="onInit" />
            <p class="decode-result">
              掃描結果: <b>{{ result }}</b
              ><b :class="{ hidden: prompt }">{{ prompttext }}</b>
            </p>
            <div class="row" v-for="(item, index) in tempindex" :key="index">
              <div class="col" :class="{ 'd-none': isshow }">
                <div>
                  <label
                    :class="[
                      textdanger === true && {
                        'text-danger': !tempscanlist[item].result,
                      },
                    ]"
                    >{{ tempscanlist[item].inspectno
                    }}{{ tempscanlist[item].inspectname }}</label
                  >
                  <div v-if="tempscanlist[item].inspecttype === '0'">
                    <div class="form-check form-check-inline">
                      <input
                        class="form-check-input"
                        type="radio"
                        :name="item"
                        id="inlineRadio1"
                        value="Y"
                        v-model="tempscanlist[item].result"
                      />
                      <label class="form-check-label" for="inlineRadio1"
                        >Ok</label
                      >
                    </div>
                    <div class="form-check form-check-inline">
                      <input
                        class="form-check-input"
                        type="radio"
                        :name="item"
                        id="inlineRadio2"
                        value="N"
                        v-model="tempscanlist[item].result"
                      />
                      <label class="form-check-label" for="inlineRadio2"
                        >Fail</label
                      >
                    </div>
                  </div>
                  <div v-else-if="tempscanlist[item].inspecttype === '1'">
                    <input
                      type="number"
                      step="0.1"
                      class="form-control"
                      v-model="tempscanlist[item].result"
                    />
                  </div>
                  <div v-else-if="tempscanlist[item].inspecttype === '2'">
                    <div class="form-group">
                      <textarea
                        class="form-control"
                        id="exampleFormControlTextarea1"
                        rows="3"
                        v-model="tempscanlist[item].result"
                      ></textarea>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <button
              id="btn1"
              class="btn btn-primary btn-block"
              @click.prevent="scandone"
              :class="{ 'd-none': isshow }"
            >
              輸入結果
            </button>
            <div class="d-flex justify-content-center mt-4">
              <div
                class="error-message alert text-center w-50"
                style="position: flex;"
                :class="{ hidden: prompt }"
              >
                {{ prompttext }}
              </div>
            </div>
          </form>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import { QrcodeStream } from "@/";
import { mapGetters, mapActions } from "vuex";
export default {
  components: { QrcodeStream },
  data() {
    return {
      result: "",
      error: "",
      lookresult: "",
      tempscanlist: [],
      tempindex: [],
      isshow: true,
      prompt: true,
      prompttext: "",
      textdanger: false,
    };
  },

  methods: {
    onDecode(result) {
      const vm = this;
      vm.tempindex = [];
      vm.result = result;
      const spIndex = vm.result.split(";");
      const chspIndex = spIndex.filter((item) => item.length > 0);
      for (const i in chspIndex) {
        const findex = vm.tempscanlist.findIndex(
          (item) => item.inspectno === chspIndex[i]
        );
        if (findex > -1) {
          vm.tempindex.push(findex);
        }
      }
      if (vm.tempindex[0] < 0) {
        vm.tempindex = [];
        vm.prompttext = "非為巡檢條碼";
        vm.prompt = false;
      }
      vm.isshow = false;
    },
    async onInit(promise) {
      try {
        await promise;
      } catch (error) {
        if (error.name === "NotAllowedError") {
          this.error = "ERROR: you need to grant camera access permisson";
        } else if (error.name === "NotFoundError") {
          this.error = "ERROR: no camera on this device";
        } else if (error.name === "NotSupportedError") {
          this.error = "ERROR: secure context required (HTTPS, localhost)";
        } else if (error.name === "NotReadableError") {
          this.error = "ERROR: is the camera already in use?";
        } else if (error.name === "OverconstrainedError") {
          this.error = "ERROR: installed cameras are not suitable";
        } else if (error.name === "StreamApiNotSupportedError") {
          this.error = "ERROR: Stream API is not supported in this browser";
        }
      }
    },
    backtrack() {
      const vm = this;
      vm.$router.push("/ScanList");
    },
    scandone() {
      const vm = this;
      let icount = "0";
      for (const i in vm.tempindex) {
        if (!vm.tempscanlist[vm.tempindex[i]].result) {
          console.log(vm.tempscanlist[vm.tempindex[i]]);
        } else {
          icount++;
        }
      }
      if (vm.tempindex.length === icount) {
        const createts = new Date(+new Date() + 8 * 3600 * 1000)
          .toISOString()
          .split(".");
        for (const i in vm.tempindex) {
          vm.tempscanlist[vm.tempindex[i]].createts = createts[0];
          vm.tempscanlist[vm.tempindex[i]].createuserid = vm.currentuser.userid;
          vm.tempscanlist[vm.tempindex[i]].createusername = vm.currentuser.username;
          vm.tempscanlist[vm.tempindex[i]].createfullname = vm.currentuser.fullname;
        }
        vm.prompttext = "儲存成功";
        vm.textdanger = false;
        vm.prompt = false;
        vm.isshow = true;
      } else {
        vm.prompttext = "尚未完成輸入結果";
        vm.textdanger = true;
        vm.prompt = false;
      }
      // if (vm.lookresult === "") {
      //   vm.prompttext = "尚未輸入內容";
      //   vm.prompt = false;
      // } else {
      //   const tempscan = [];
      //   const createts = new Date(+new Date() + 8 * 3600 * 1000)
      //     .toISOString()
      //     .split(".");
      //   tempscan.push(vm.tempindex);
      //   tempscan.push(vm.lookresult);
      //   tempscan.push(vm.currentuser.userid);
      //   tempscan.push(vm.currentuser.username);
      //   tempscan.push(createts[0]);
      //   tempscan.push(vm.currentuser.fullname);
      //   vm.scandonetemp(tempscan);
      //   vm.prompttext = "儲存成功";
      //   vm.prompt = false;
      //   vm.lookresult = "";
      //   vm.isshow = true;
      // }
    },
    // ...mapActions(["scandonetemp"]),
  },
  watch: {
    prompt: function() {
      const vm = this;
      setTimeout(() => {
        vm.prompt = true;
      }, 3000);
    },
  },
  computed: {
    ...mapGetters(["scanlist", "currentuser"]),
  },
  created() {
    const vm = this;
    vm.tempscanlist = vm.scanlist;
    // vm.onDecode(
    //   "R201-001;R201-002;R201-003;R201-004;R201-005;R201-006;R201-007;R201-008;R201-009;R201-010;R202-001;R202-002;R202-003;R202-004;R202-005;R202-006;R202-007;"
    // );
    // vm.onDecode("R202-003;");
  },
};
</script>

<style scoped>
.error {
  font-weight: bold;
  color: red;
}
</style>
